  --ani-speed: 10s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.products-carousel-wrapper:before, .products-carousel-wrapper:after {
  content: "";
  z-index: 1;
  width: 20%;
  height: 100%;
  position: absolute;
  top: 0;
}

.products-carousel-wrapper:before {
  background-image: linear-gradient(to right, var(--bg-clr) 0%, transparent 50%);
  left: 0;
}

.products-carousel-wrapper:after {
  background-image: linear-gradient(to left, var(--bg-clr) 0%, transparent 50%);
  right: 0;
}

.products-carousel-list {
  animation: slide var(--ani-speed) linear infinite;
  align-items: center;
  display: flex;
}

.products-carousel-item {
  flex: 1 0 var(--width);
  text-align: center;
  padding: 1rem;
}

.products-carousel-item > .products-card {
  object-fit: cover;
  width: 100%;
  height: auto;
}

@keyframes slide {
